.Project {
  width: 100vw;
  height: 100vh;
  background: #0d0d0d;
  display: flex;
  overflow-y: hidden;
  .inner {
    width: 75%;
    height: 100vh;
    margin: 0 auto;
    .project01 {
      padding-top: 75px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .text {
        color: #fff;
        display: flex;
        flex-direction: column;
        font-family: 'Pretendard-Regular';
        font-weight: 700;
        font-style: normal;
        h3 {
          font-size: 20px;
        }
        h1 {
          font-size: 30px;
          padding: 30px 0;
        }
        h4 {
          padding-bottom: 30px;
          line-height: 1.5;
          font-weight: normal;
        }
        ul {
          display: flex;
          li {
            color: #6c6c6c;
            font-weight: normal;
            font-size: 14px;
            padding-right: 10px;
          }
        }
        .btn {
          margin-top: 30px;
          color: #fff;
          border: 1px solid #fff;
          box-sizing: border-box;
          width: 105px;
          height: 35px;
          border-radius: 30px;
          line-height: 35px;
          text-align: center;
          transition: .5s;
          &:hover {
            background: #fff;
            color: #0d0d0d;
          }
        }
      }
      .img {
        margin: 5vh 0;
        width: 37.5vw;
        height: 80vh;
        background-image: linear-gradient(rgba(0,0,0,.5),rgba(0,0,0,.5)),url('../../../public/img/mock.png');
        background-repeat: no-repeat;
        background-size: 100%;
      }
    }
  }
}