.Header {
  width: 100vw;
  background: #0d0d0d;
  .resume {
    height: 10vh;
    background: #0d0d0d;
    position: fixed;
    top: 0; left: 0;
    z-index: 1;
    width: 100%;
    display: flex;
    .inner {
      width: 75%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 auto;
      h1 {
        font-size: 22px;
        color: #fff;
        font-family: "salo-variable",sans-serif;
      }
      .menu {
        ul {
          display: flex;
          color: #fff;
          li {
            margin-left: 50px;
            font-family: "salo-variable",sans-serif;
          }
        }
      }
    }
  }
}