.Contact {
  width: 100vw;
  height: 100vh;
  background: #0d0d0d;
  .inner {
    width: 75%;
    height: 100vh;
    margin: 0 auto;
    .content {
      padding-top: 75px;
      h1 {
        color: #fff;
        font-size: 50px;
        font-family: "salo-variable",sans-serif;
        font-weight: 700;
        padding-top: 30px;
      }
      .text {
        font-family: 'Pretendard-Regular';
        display: flex;
        align-items: center;
        height: 75vh;
        justify-content: space-between;
        h1 {
          height: 70vh;
          display: flex;
          align-items: center;
          padding-right: 100px;
          border-right: 1px solid #fff;
          padding-top: 0;
          font-size: 26px;
        }
        ul {
          display: flex;
          flex-direction: column;
          li {
            display: flex;
            flex-direction: column;
            align-items: center;
            color: #fff;
            border-bottom: 1px solid #fff;
            padding: 50px 0;
            padding-left: 750px;
          }
          li.insta {
            border: none;
            transition: .5s;
            padding-right: 50px;
            &:hover {
              background: rgba(255,255,255,.2);
            }
          }
          li.git {
            transition: .5s;
            padding-right: 50px;
            &:hover {
              background: rgba(255,255,255,.2);
            }
          }
          li.mail {
            padding-right: 50px;
            transition: .5s;
            &:hover {
              background: rgba(255,255,255,.2);
            }
          }
        }
      }
    }
  }
}