.Home {
  width: 100vw;
  height: 100vh;
  background: #0d0d0d;
  .home {
    position: relative;
    top: 0; left: 0;
    background: #0d0d0d;
    height: 100vh;
    .inner {
      h1 {
        text-align: center;
        color: #fff;
        position: absolute;
        top: 50%; left: 50%;
        transform: translate(-50%,-50%);
        font-size: 115px;
        font-family: "joly-text", sans-serif;
        font-weight: 900;
        font-style: italic;
      }
      ul {
        position: absolute;
        top: 70%; left: 50%;
        transform: translate(-50%,-50%);
        display: flex;
        align-items: center;
        line-height: 30px;
        justify-content: center;
        width: 370px;
        height: 30px;
        background: #fff;
        li {
          color: #044BD9;
          font-size: 20px;
          font-family: "salo-variable",sans-serif;
          font-weight: 700;
          font-style: normal;
        }
      }
    }
  }
}