.About {
  width: 100vw;
  height: 100vh;
  background: #0d0d0d;
  .inner {
    width: 75%;
    height: 100vh;
    margin: 0 auto;
    .content01 {
      padding-top: 75px;
      h1 {
        color: #fff;
        font-size: 50px;
        padding-top: 30px;
        font-family: "salo-variable",sans-serif;
        font-weight: 700;
      }
    }
    .content02 {
      font-family: 'Pretendard-Regular';
      display: flex;
      align-items: center;
      height: 75vh;
      .imgBox {
        display: flex;
        align-items: start;
        flex-direction: column;
        .img {
          width: 200px;
          height: 250px;
          border: 1px solid  #fff;
        }
        .description {
          color: #fff;
          .name {
            padding: 15px 0;
            width: 200px;
            font-size: 20px;
            text-align: center;
            span {
              font-size: 14px;
              padding-left: 10px;
              color: #6c6c6c;
            }
          }
          .buttom {
            display: flex;
            align-items: center;
            .icon {
              display: flex;
              flex-direction: column;
              align-items: start;
            }
            .text {
              display: flex;
              flex-direction: column;
              line-height: 1.8;
              .tel {
                display: flex;
                align-items: center;
              }
              .mail {
                display: flex;
                align-items: center;
              }
              .addr {
                display: flex;
                align-items: center;
              }
            }
          }
        }
      }
      .me {
        color: #fff;
        margin-left: 45px;
        display: flex;
        flex-direction: column;
        height: 52vh;
        .up {
          flex: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .down {
          display: flex;
          >h1 {
            width: 80px;
            height: 30px;
            text-align: center;
            line-height: 30px;
            border-radius: 10px;
            border: 1px solid #044BD9;
          }
          .left {
            display: flex;
            .date {
              margin-right: 15px;
              margin-left: 15px;
              li {
                padding-bottom: 38px;
              }
              :nth-child(1) {
                margin-top: 7.5px;
              }
            }
            .prize {
              li {
                h1 {
                  
                }
                :nth-child(1) {
                  margin-top: 7.5px;
                }
                p {
                  padding: 10px 0;
                  padding-bottom: 15px;
                  color: #6c6c6c;
                  font-size: 14px;
                }
              }
            }
          }
          .right {
            margin-left: 45px;
            display: flex;
            flex-direction: column;
            .license {
              display: flex;
              h1 {
                width: 80px;
                height: 30px;
                text-align: center;
                line-height: 30px;
                border-radius: 10px;
                border: 1px solid #044BD9;
              }
              .date2 {
                margin-right: 15px;
                margin-left: 15px;
                li {
                margin-top: 7.5px;
                padding-bottom: 38px;
                }
              }
              .licenseName {
                li {
                  display: flex;
                  padding-bottom: 38px;
                  margin-top: 7.5px;
                  p {
                    margin-top: 3px;
                    color: #6c6c6c;
                    font-size: 14px;
                    margin-left: 7px;
                  }
                }
              }
            }
            .main {
              display: flex;
              h1 {
                width: 80px;
                height: 30px;
                text-align: center;
                line-height: 30px;
                border-radius: 10px;
                border: 1px solid #044BD9;
              }
              h1:nth-child(3) {
                margin-left: 30px;
              }
              p {
                line-height: 30px;
                margin-left: 15px;
              }
            }
          }
        }
      }
    }
  }
}